<template>
  <div class="goods-container">
    <el-button @click="dialogVisible = true">
      /
    </el-button>
    <el-dialog
      :visible.sync="dialogVisible"
      width="460px"
      center
      :before-close="close"
    >
      <el-form
        label-width="80px"
      >
        <el-form-item label="">
          <el-upload
            style="display:inline"
            action
            :limit="1"
            :http-request="detailImgsUpload"
            :on-remove="removeFile"
            list-type="picture-card"
            ref="uploadTwo"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <div style="width:300px">
            <el-input v-model="url" />
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="status"
            clearable
            placeholder=""
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in statusList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="type"
            clearable
            placeholder=""
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in typeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="position"
            clearable
            placeholder=""
            style="width:300px"
          >
            <el-option
              v-for="(item,index) in positionList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div style="width:300px">
            <el-input v-model="sort" />
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close"> </el-button>
        <el-button
          type="primary"
          @click="confirm"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            dialogVisible: false,
            // /
            imageUrl:"",
            // 
            url:"",
            //  
            statusList:[
                {
                    "id": 1, 
                    "name": ""
                }, 
                {
                    "id": 0, 
                    "name": ""
                }
            ],
            status:"",
            // 
            typeList:[
                {
                    "id": 1, 
                    "name": ""
                }, 
                {
                    "id": 2, 
                    "name": ""
                }, 
                {
                    "id": 3, 
                    "name": ""
                }
            ],
            type:"",
            // 
            positionList:[
                {
                    "id": 1, 
                    "name": ""
                }, 
                {
                    "id": 2, 
                    "name": ""
                }
            ],
            position:"",
            // 
            sort:""
        };
    },
    // computed: {
    //   // 
    //   uploadDisabled:function() {
    //       return this.image_url.length >0
    //   },
    // },
    methods: {
        // 
        async detailImgsUpload (file){
            let url =  await setImgName( client(),file);
            this.imageUrl = url;
        },
        //  
        removeFile(){
            this.imageUrl = "";
        },
        // 
        async confirm (){
            if(this.image_url === ""){
                this.$message({
                    message: "/",
                    type: "warning"
                });
                return;
            }
            // if(this.url === ""){
            //     this.$message({
            //         message: "",
            //         type: "warning"
            //     });
            //     return;
            // }
            if(this.status === ""){
                this.$message({
                    message: "",
                    type: "warning"
                });
                return;
            }
            
            if(this.type === ""){
                this.$message({
                    message: "",
                    type: "warning"
                });
                return;
            }
            if(this.position === ""){
                this.$message({
                    message: "",
                    type: "warning"
                });
                return;
            }
            if(this.sort === ""){
                this.$message({
                    message: "",
                    type: "warning"
                });
                return;
            }
            try {
                let res = await this.axios.post("admin/addbanner",{
                    "image_url":this.imageUrl, // /
                    "url": this.url, // 
                    "status":this.status, //  
                    "type":this.type, // 
                    "position":this.position, // 
                    "sort":this.sort // 
                });
                
                this.close();
                this.$emit("getData");
                this.$message({
                    message:res.msg,
                    type:"success"
                });
            } catch (error) {
                console.log(error);
            }
        },
        close (){ 
            // 
            this.imageUrl = "";
            this.url = "";
            this.status = "";
            this.type = "";
            this.position = "";
            this.sort = "";
            this.dialogVisible = false;
            this.$refs["uploadTwo"].clearFiles();
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.goods-container{
  .size{
    display: flex;
    justify-content: space-between;
    width: 450px;
    margin-bottom: 10px;
    .size-item{
      width: 270px;
    }
  }
}
</style>
